.formDiv {
    padding-left: 100px;
    padding-right: 80px;
}

.formSection {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    width: 100%;   
}

.logoContainer {
    flex: 0.5;
    height: 700px;
}

.logoImg {
    margin-left: -22px;
    margin-top: 30px;
    height: 600px;
    width: 500px;
}
.formContainer {
    flex: 0.5;
}
.formWrapper {
    margin-left: 80px;
    margin-top: 30px;
    box-shadow: 0 0 2px rgba(0,0,0,0.3);
    width: 360px;
    padding-left: 40px;
    padding-top: 40px;
    padding-bottom: 40px;
    border-radius: 5px;
}


input[type = "text"]{
    width: 296px;
    height: 30px;
    padding: 5px 10px;
    border: 1px solid #ff3e6c;
    border-radius: 5px;
    margin-bottom: 20px;
}

input[type = "text"]:focus {
    outline-style: none;
    box-shadow:  0px 0px 3px #ff3e6c !important;
  }


.gen-row {
      display: flex;
      padding: 0;
      margin-left: 10px;
  }
input[type="radio"] {
    opacity: 0;
    position: fixed;
    width: 0;
  }
  
label {
      display: inline-block;
      padding: 5px 10px;
      font-size: 14px;
      width: 50px;
      border: 1px solid ff3e6c;
      border-radius: 4px;
      margin-right: 20px;
      text-align: center;
      border: 1px solid #ff3e6c;
  }
  
label:hover {
    background-color: rgba(0,0,0,0.07);
    border: 0px solid #ff3e6c;
  }
  
  
 input[type="radio"]:checked + label {
      background-color: #ff3e6c;
      color: white;
  }

  .genderCol {
      display: flex;
      flex-direction: row;
      text-align: left;

  }

  .genderLabel {
      margin-top: -4px;
      margin-left: -10px;
  }



select {
    margin-top: 20px;
}

.select-color {
    width: 317px;
    height: 40px;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #ff3e6c;
    border-radius: 5px;
    background-color: white;
    
}
.select-brand {
    width: 150px;
    height: 40px;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #ff3e6c;
    border-radius: 5px;
    background-color: white;
    margin-right: 20px;
}
.select-category {
    width: 150px;
    height: 40px;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #ff3e6c;
    border-radius: 5px;
    background-color: white;
    margin-right: 20px;
}

select option:hover {
    box-shadow: 0 0 10px 100px #ff3e6c inset !important;
}
select:focus > option:checked { 
    background: white !important;
}

select option {
    background-color: #ff3e6c;
}


.sizeWrapper {
    width: 314px;
    margin-top: 25px;
    border: 1px solid #ff3e6c;
    position: relative;
    border-radius: 5px;
    padding :7px 0px;
    margin-bottom: 20px;
}

.sizeLabel {
    position: absolute;
    left: 9px;
    font-size: 12px;
    background-color: white;
    padding: 0 4px;
    top: -9px;
    color: #ff3e6c;
}
.sizeBoxWrapper {
    padding: 0 30px;
    padding-top: 10px;    
}


.sizeBox {
    display: inline-block;
    margin-right: 18px;
    border-radius: 50%;
    height: 25px;
    padding-top: 5px;
    width: 29px;
    color: #ff3e6c;
    font-weight: bold;
    font-size: 12px;
    border: 1px solid #ff3e6c;
    margin-bottom: 10px;
    text-align: center;
}

.sizeBox-active {
    display: inline-block;
    margin-right: 18px;
    border-radius: 50%;
    height: 25px;
    padding-top: 5px;
    width: 29px;
    color: white;
    font-weight: bold;
    font-size: 12px;
    border: 1px solid #ff3e6c;
    margin-bottom: 10px;
    text-align: center;
    background-color: #ff3e6c;
}

.fileName {
    width: 180px;
    height: 30px;
    padding: 5px 10px;
    border: 1px solid #ff3e6c;
    margin-right: 10px;
    border-radius: 5px;
    margin-bottom: 20px;
}

.uploadButton {
    width: 100px;
    background-color: #ff3e6c;
    border: none;
    color: white;
    border-radius: 5px;
    height: 43px;
}

.submitBut {
    width: 314px;
    background-color: #ff3e6c;
    border: none;
    color: white;
    border-radius: 5px;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 3px;
    height: 45px;
}

input[type = "file"]{
    width: 296px;
    height: 30px;
    padding: 8px 10px;
    border: 1px solid #ff3e6c;
    border-radius: 5px;
    margin-bottom: 20px;
}