.productDiv {
    padding-left: 100px;
    padding-right: 80px;
}

.productSection {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    width: 100%;   
}

.imageContainer {
    flex: 0.5;
    display: flex;
}

.detailContainer {
    flex: 0.5;
    height: 700px;
    margin-left: 80px;
}

.primeContainer {
    flex: 0.7;
}

.secondContainer {
    flex: 0.3;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.primeImg {
    width: 450px;
    height: 600px;
    cursor: zoom-in;
}

.secondImg {
    width: 100px;
    height: 110px;
    margin-bottom: 10px;
    cursor: zoom-in;
}

.prodName {
    font-weight: bold;
    color: black;
    font-size: 20px;
    margin-bottom: 5px;
}

.prodBrand {
    color: rgba(0,0,0,0.5);
    font-size: 18px;
    margin-bottom: 10px;
}

.prodPrice {
    font-size: 20px;
    font-weight: bold;
    display: inline-block;
    width: 80px;

}

.wishlist {
    margin-left: 20px;
    width: 100px;
    text-align: center;
    padding: 3px 10px;
    border: 1px solid #ff3e6c;
    border-radius: 7px;
    font-size: 14px;

}
.sizeWrapper {
    width: 314px;
    margin-top: 25px;
    border: 1px solid rgba(0,0,0,0.3);
    position: relative;
    border-radius: 5px;
    padding :7px 0px;
}

.sizeLabel {
    position: absolute;
    left: 9px;
    font-size: 12px;
    background-color: white;
    padding: 0 4px;
    top: -9px;
    color: black;
    font-weight: 600;
}
.sizeBoxWrapper {
    padding: 0 30px;
    padding-top: 10px;

}

.sizeBox {
    display: inline-block;
    margin-right: 18px;
    border-radius: 50%;
    height: 25px;
    padding-top: 5px;
    width: 29px;
    color: black;
    font-weight: bold;
    font-size: 12px;
    border: 1px solid rgba(0,0,0,0.3);
    text-align: center;
}

.buyButton {
    width: 300px;
    border: none;
    text-transform: uppercase;
    font-size: 18px;
    color: white;
    text-align: center;
    height: 50px;
    background-color: #ff3e6c;
    border-radius: 7px;
}
.sizeChartLink {
    margin-top: -15px;
}
.sizeChart {
    font-size: 14px;
    color: black;
    text-decoration: none;
}

.sizeChart:hover {
    color: #ff3e6c;
}

.prodDescrip {
    margin-top: 20px;
    width: 400px;
    font-size: 14px;
    color: rgba(0,0,0,0.5);
}

.desHead {
    font-weight: 600;
    font-size: 16px;
    color: black;
}


.delWrapper {
    width: 340px;
    height: 200px;
    border: 1px solid rgba(0,0,0,0.3);
    margin-top: 20px;
    position: relative;
    padding-left: 20px;
    font-size: 14px;
    color: rgba(0,0,0,0.8);
    line-height: 22px;
    border-radius: 5px;
}

.delLabel {
    font-size: 12px;
    width :100px;
    background-color: white;
    color: black;
    font-weight: 600;
    position: absolute;
    left: 12px;
    top: -10px;
    text-align: center;
}

.area input {
    width: 180px;
    margin-top: 20px;
    margin-bottom: 7px;
    height: 20px;
}

.checkButton {
    height: 31px;
    background-color:#ff3e6c;
    color: white;
    width: 100px;
    border: none;
    border-radius: 100px;
}