.listDiv {
    padding-left: 100px;
    padding-right: 80px;
}

.listSection {
    margin-top: 50px;
    flex-direction: row;
    width: 100%;   
}



.products {
    display: flex;
    flex-direction: column;
    padding: 20px 40px;
}

.productRow {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;

}

.singleProduct {
    display: flex;
    flex-direction: column;
    width: 180px;
    height: 300px;
    box-shadow: 0 0 2px rgba(0,0,0,0.5);
    margin-right: 50px;
    border-radius: 5px;
}

.listImage {
    width: 180px;
    height: 180px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    margin-bottom: 10px;
}

.listBrand {
    padding-left: 10px;
    font-weight: 600;
    font-size: 14px;
    margin-bottom: 5px;
}

.listInfo1 {
    padding-left: 10px;
    font-size: 14px;
}

.viewMore {
    text-decoration: none;
    color: #ff3e6c;
    margin-left: 50px;
    font-size: 14px;
    font-weight: 600;
    margin-top: 7px;
}

.viewMore:hover {
    text-decoration: underline;
}